html{
    overflow: hidden;
}
html.overflow{
    overflow-x: hidden;
    overflow-y: visible;
}
button:focus,
*:focus {
    outline: none;
}
body {
    font-family: 'Rubik',sans-serif;
    margin: 0px;
    padding: 0px;
    font-size: 12px;
    line-height: 1.8;
    font-weight: 400;
    color: #000000;
    background: #ffffff;
    border-color: #dfdfdf;
    transition: -webkit-transform ease-in .4s;
    transition: transform ease-in .4s;
    transition: transform ease-in .4s, -webkit-transform ease-in .4s;
    overflow-x: hidden;
}
button {
    background: transparent;
    border: none;
    padding: 0;
}
label {
    line-height: normal;
}
.menu-open{
    background-color: #000000ab;
}
.align-item-center {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
}
.p-relative {
    position: relative;
}
.custom-container{
    padding-right: 100px;
    padding-left: 100px;
}
.before-none:before,
.after-none:after,
.none {
    display: none;
}
.overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
}
.overlay-bg{
    background: #000;
    opacity: 0.5;
}
.overlay-2 {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
}
.image-fit{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}
.transform-center {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    -webkit-transform: translate(0%, -50%);
            transform: translate(0%, -50%);
    z-index: 1;
}
[class^="flaticon-"]:before,
[class*=" flaticon-"]:before {
    font-size: inherit;
    margin: 0;
}
.animate-img{
    position: relative;
    overflow: hidden;
}
.animate-img:hover img{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 0.5s;
}
/*preloader*/
.loader{
    overflow: hidden;
}
.preloader{
    background: #fd5d14;
    height: 100vh;
    width: 100%;
    position: absolute;
    top: 0;
    z-index: 99;
    overflow: hidden;
}
.preloader img{
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    margin: 0 auto;
}
/*section-header*/
.section-header{
    max-width: 540px;
    padding-bottom: 30px;
    margin: 0 auto;
    text-align: center;
    position: relative;
    z-index: 10;
}
.section-header .section-heading{
    position: relative;
}
.section-header .section-heading:after{
    content: "";
    height: 5px;
    width: 80px;
    background-color: #fd5d14;
    position: absolute;
    top: auto;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    margin: 0 auto;
}
.section-header .section-heading h5{
    font-weight: 600;
    margin-bottom: 10px;
    text-transform: uppercase;
}
.section-header .section-heading h3{
    padding-bottom: 20px;
    font-weight: 600;
}
.section-header .section-description{
    margin-bottom: 20px;
    position: relative;
}
/*section heading right*/
.section-header-right{
    max-width: 550px;
    padding-bottom: 30px;
    text-align: right;
}
.section-header-right .section-heading{
